import api from '../../http/api.js'
export default {
	// 
	namespaced: true,
	state: {
		cateData: '',
	},
	mutations: {
		setCateData(state, value) {
			state.cateData = value
		},
	


	},
	// 
	actions: {
		// : 
		// action store 
		// commit=  this.state.commit
		async getCategory({
			commit
		}, params) {
			try {
				let res = await api.getCategory()
				if (res.code===200) {
					// 
					let data=[]
					res.data.map(i=>{
						data.push({...i,title:i.name})
					})
					// console.log(data)
					commit('setCateData',data)
				}
			} catch (err) {
				console.log(err)
			}
		},
		
		
	}
}
