import dayjs from 'dayjs'






const amapFile = require('../lib/amap-wx.js')
const key = 'd7549ad51cff75fb27df6434d5e44173'
const myAmapFun = new amapFile.AMapWX({
	key
})



export default {
	countdown(time1, time2) {

		let now = dayjs().valueOf()
		let t1 = dayjs(time1).valueOf()
		let t2 = dayjs(time2).valueOf()
		let c1 = now - t1
		let c2 = (t2 - t1)
		let pre = (c1 / c2) * 100
		if (now > t1) {
			if (c1 / c2 >= 1) {
				return 100
			} else {
				let c = Number(pre.toFixed(0))
				return c
			}
		} else {
			return 0
		}

	},
	// 
	// key: key 
	// next:  
	// item: next 
	checkLogin({
		key,
		next,
		item
	}) {
		// 
		let user = uni.getStorageSync(key)
		if (user) {
			next(item)
		} else {
			uni.showModal({
				title: '',
				content: '',
				confirmText: '',
				success(res) {
					if (res.confirm) {
						uni.navigateTo({
							url: '/pages/login/login'
						})
					}
				}
			})
			// // 
			// Dialog.confirm({
			//   title: '',
			//   message: '?',
			// }).then(() => {
			//   // 
			//   router.push('/Login')
			// }).catch(() => {
			//   Toast('')
			// })
		}
	},

	// 
	getLocation(LngLat, name) {




























































































		return new Promise((resovle, reject) => {
			myAmapFun.getRegeo({
				success: data => {
					//
					resovle({
						code: 200,
						msg: '',
						data
					})
					// console.log(data)
				},
				fail: info => {
					//
					reject({
						code: 500,
						msg: '',
						info
					})
					console.log(info)
				}
			})
		})

	},

	// 
	saveHistory({
		key,
		data,
		attr
	}) {
		// key data, 
		// attr   
		//  
		//   userid  
		let name
		let userId = uni.getStorageSync('userId')
		if (userId) {
			name = key + 'History' + userId
		} else {
			name = key + 'History'
		}
		// 	
		let history = uni.getStorageSync(name)
		// null undefind
		// 
		let stu;
		//  
		if (history) {
			//
			stu = uni.getStorageSync(name)
			// 
			stu.unshift(data);
		} else {
			//
			stu = []; //
			stu.unshift(data);
		}
		//
		if (attr) {
			const res = new Map();
			stu = stu.filter(stu => !res.has(stu[attr]) && res.set(stu[attr], 1))
		} else {
			stu = Array.from(new Set(stu))
		}
		// localStorage[name] = stuStr;
		uni.setStorageSync(name, stu)

	},
	// 
	delhistory(key) {
		let name
		let userId = uni.getStorageSync('userId')
		if (userId) {
			name = key + 'History' + userId
		} else {
			name = key + 'History'
		}
		uni.removeStorageSync(name)
	},


	// 
	delHistoryOne({
		key,
		value,
		// id
		id
	}) {
		// key  value  id
		//   userid  
		let name
		let userId = uni.getStorageSync('userId')
		if (userId) {
			name = key + 'History' + userId
		} else {
			name = key + 'History'
		}
		let stu = uni.getStorageSync(name)
		if (stu) {
			if (id) {
				stu = stu.filter((a) => {
					return a[id] !== value[id];
				});
			} else {
				stu = stu.filter((a) => {
					return a !== value;
				});
			}
			if (stu.length === 0) {
				uni.removeStorageSync(name)
			} else {
				uni.setStorageSync(name, stu)
			}
		} else {
			return
		}
	},
	//  index
	delMapOne({
		key,
		index,
	}) {
		// key  value  id
		//   userid  
		let name
		let userId = uni.getStorageSync('userId')
		if (userId) {
			name = key + 'History' + userId
		} else {
			name = key + 'History'
		}
		let stu = uni.getStorageSync(name)
		// 
		if (stu) {
			stu.splice(index, 1)
			uni.setStorageSync(name, stu)
			if (stu.length === 0) {
				uni.removeStorageSync(name)
			} else {
				uni.setStorageSync(name, stu)
			}
		} else {
			return
		}
	},
	// 
	// key
	getHistory(key) {
		//   userid  
		let name
		let userId = uni.getStorageSync('userId')
		if (userId) {
			name = key + 'History' + userId
		} else {
			name = key + 'History'
		}
		let arr = uni.getStorageSync(name)
		if (arr) return arr
		else return null
	},

	//
	// loadash  
	// uniqWith(title.value, isEqual)
}
