// flyio


import Http from 'flyio/dist/npm/wx'






let http = new Http

http.config.baseURL = 'http://localhost:7001/'

http.interceptors.request.use(config => {
	uni.showLoading({
		title: '...'
	})
	let token = uni.getStorageSync('liftToken')
	if (token) {
		config.headers.Authorization = token
	}
	return config
}, err => {
	uni.hideLoading()
	return Promise.reject(err)
})

http.interceptors.response.use(res => {
	uni.hideLoading()
	return res.data

}, err => {
	uni.hideLoading()
	// 
	let status = err.response && err.response.status
	if (status === 400) {
		uni.showToast({
			title: '',
			icon: 'none'
		})
	}
	if (status === 401) {
		uni.showToast({
			title: '',
			icon: 'none'
		})
		uni.clearStorage()
		uni.navigateTo({
			url:'/pages/login/login'
		})
	}
	if (status === 403) {
		uni.showToast({
			title: '',
			icon: 'none'
		})
	}
	if (status === 404) {
		uni.showToast({
			title: '',
			icon: 'none'
		})
	}
	if (status === 500) {
		uni.showToast({
			title: '',
		})

	}
	if (status === 503) {
		uni.showToast({
			title: '',
			icon: 'none'
		})
	}
	if (status === 422) {
		// 422	
		uni.showToast({
			title: '',
			icon: 'none'
		})
	}

	console.log(err)
	return Promise.reject(err)
})

export default http
