import api from '../../http/api.js'
export default {
	// 
	namespaced: true,
	state: {
		saechData: [],
		searchList: [],
		seachTotal: 0,
	},
	mutations: {
		setSaechData(state, value) {
			state.saechData = value
		},
		setSearchList(state, value) {
			state.searchList = value
		},
		setSeachTotal(state, value) {
			state.seachTotal = value
		},


	},
	// 
	actions: {
		// : 
		// action store 
		// commit=  this.state.commit
		async getSearchWords({
			commit
		}, params) {
			try {
				let res = await api.getSearchWords()
				if (res.code === 200) {
					// console.log(res);
					// 	// 
					commit('setSaechData', res.data)
				}
			} catch (err) {
				console.log(err)
			}
		},

		async search({
			commit
		}, params) {
			try {
				let res = await api.search(params)
				// console.log(params);
				if (res.code === 200) {
					// 	// 
					commit('setSearchList', res.data)
					commit('setSeachTotal', res.total)
				}
			} catch (err) {
				console.log(err)
			}
		},



	}
}
